import React from "react";
import Modal from "react-modal";
import { RouteComponentProps } from "react-router-dom";
Modal.setAppElement("#root");
const customStyles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: "rgba(0,0,0,0.8)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    zIndex: 10001
  },
  content: {
    position: "relative",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    border: 0,
    padding: 0,
    backgroundColor: "#fff",
    maxWidth: "480px",
    maxHeight: "100vh",
    borderRadius: "4px",
    overflowY: "auto",
    boxSizing: "border-box"
  }
};
export default (props: RouteComponentProps) => {
  const [modalIsOpen, setModalIsOpen] = React.useState(true);
  const open = () => setModalIsOpen(true);
  const close = () => setModalIsOpen(false);
  const join = () => {
    props.history.push("/join");
    setModalIsOpen(false);
  };
  return (
    <Modal
      isOpen={modalIsOpen}
      onAfterOpen={open}
      onRequestClose={close}
      style={customStyles}
      contentLabel="Opening Modal"
      closeTimeoutMS={300}
    >
      <header className="modal__header">
        <h2 className="modal__title">
          Enough,
          <br />
          Zuck
        </h2>
        <button className="modal__close" onClick={close}></button>
      </header>
      <main className="modal__content">
        <p>
          Facebook makes billions every year from our personal data. What do we
          get?
        </p>
        <p>
          A like? Disinformation campaigns? Misuse of our personal data? Another
          rant from Uncle Joe?
        </p>
        <p>We demand more for using Facebook.</p>
      </main>
      <footer className="modal__footer">
        <button onClick={join}>Join Boycott</button>
        <button className="secondary-btn" onClick={close}>
          Not Now
        </button>
      </footer>
    </Modal>
  );
};
