import * as React from "react";
import { Link } from "react-router-dom";
import { Share } from "react-facebook";
import { RouteComponentProps } from "react-router-dom";
const buttonStyle: React.CSSProperties = {
  backgroundColor: "transparent",
  color: " #0a0c0f",
  textTransform: "uppercase",
  lineHeight: "52px",
  textDecoration: "none"
};
export default (props: RouteComponentProps) => {
  const [chk, setChk] = React.useState(false);
  function toHome() {
    props.history.push("/");
    // checkbox.current!.setAttribute("value", )
    // checkbox.current!.toggleAttribute("value");
    setChk(!chk);
  }
  return (
    <div className="header">
      <div className="container">
        <div className="row">
          <div className="columns three">
            <nav role="navigation">
              <div id="menuToggle">
                <input
                  type="checkbox"
                  checked={chk}
                  onChange={() => setChk(!chk)}
                />
                <span></span>
                <span></span>
                <span></span>

                <ul id="menu">
                  <div onClick={toHome} className="uu-logo-mark" />
                  {[["Home"], ["About"], ["Info"], ["Contact"]].map(
                    ([label]) => (
                      <li
                        key={label}
                        onClick={toHome}
                        style={{
                          backgroundColor: "transparent",
                          cursor: "pointer",
                          color: "#1EAEDB",
                          textDecoration: "underline"
                        }}
                      >
                        {label}
                      </li>
                    )
                  )}
                </ul>
              </div>
            </nav>
            <div className="uu-logo">
              <Link to="/" style={{ ...buttonStyle, fontSize: 20 }}>
                <strong>USERS</strong>UNITED
              </Link>
            </div>
            <div className="mobile-login-btn">
              <Link to="/join">
                <span>
                  <i className="fa fa-lock"></i>
                </span>
              </Link>
            </div>
          </div>
          <div className="menu columns nine">
            <span style={{ ...buttonStyle, fontSize: 20 }}>
              #BOYCOTT<strong>FACEBOOK</strong>
            </span>
            <ul className="u-pull-right">
              <li className="menu-btn">
                <Share href="http://www.facebook.com">
                  {({
                    handleClick,
                    loading
                  }: {
                    handleClick: (event: any) => void;
                    loading: boolean;
                  }) => (
                    <button
                      type="button"
                      disabled={loading}
                      style={buttonStyle}
                      onClick={handleClick}
                    >
                      <span>Share</span>
                      <i className="fa fa-share"></i>
                    </button>
                  )}
                </Share>
              </li>
              <li className="menu-btn">
                <Link
                  to="/join"
                  style={{
                    fontSize: 16,
                    fontFamily: "'Michroma', sans-serif",
                    lineHeight: "52px"
                  }}
                >
                  <span>Login</span>
                  <i className="fa fa-lock"></i>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};
